/**
* SLIDE GALLERY
*/

.card_slide{
      width: 32rem;
      height: 40rem;
      position: relative;
      padding: .5rem;
      background: var(--bg-200);
      border-radius: .4rem;
      overflow: hidden;
      align-items: center;
      justify-content: center;
}

.card_slide:before{
      width: 300%;
      height: 250%;
      background: conic-gradient(var(--light), transparent, transparent, var(--primary));
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(0);
      animation: rotate_bg 4s linear infinite;
}

@keyframes rotate_bg{
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

.slick-slide{
      margin: 0 .5rem;
}

.card_slide__img{
      width: 31rem;
      height: 39rem;
      position: relative;
      border-radius: .4rem;
      overflow: hidden;
      align-items: center;
      justify-content: center;
}

.card_slide__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.btn_zoom{
      width: 5rem;
      height: 5rem;
      position: absolute;
      top: calc(50% - 2.5rem);
      left: calc(50% - 2.5rem);
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.3);
      border-radius: 50%;
      transform: scale(0);
      transition: .2s ease-in-out;
}

.card_slide:hover .btn_zoom{
      transform: scale(1);
}

.btn_zoom *{
      color: var(--light);
      font-size: 2rem;
}


.slick-slider .slick-prev,
.slick-slider .slick-next{
      position: absolute;
      top: 105%;
      width: 3rem;
      height: 3rem;
      border: solid .1rem var(--border);
      display: flex;
      align-items: center;
      justify-content: center;
      color: transparent;
      border-radius: 50%;
}

.slick-slider .slick-prev:before,
.slick-slider .slick-next:before{
      width: 1rem;
      height: 1rem;
      color: transparent;
      border-left: solid .2rem var(--primary);
      border-bottom: solid .2rem var(--primary);
}

.slick-slider .slick-prev{
      transform: translate(30rem, 0)
}

.slick-slider .slick-prev:before{
      transform: rotate(45deg);
      top: .8rem;
      left: 1rem;
}

.slick-slider .slick-next{
      transform: translate(-30rem, 0)
}

.slick-slider .slick-next:before{
      transform: rotate(225deg);
      top: .8rem;
      left: .5rem;
}